export default function (state = properties, action) {
  switch (action.type) {

    case FETCH_PROPERTIES:
      // return _.mapKeys(properties.properties, 'id');

      return {...state.properties};

    case FETCH_PROPERTY_DETAILS:
      // const propertiesObj = _.mapKeys(properties.properties, 'id');
      return {
        ...state,
        properties: state.properties.filter(property => property.id === Number(action.payload))
      };

    default:
      return state;
  }
}

